html {
    font-family: 'Roboto', sans-serif; }

div.app {

    display: flex;
    flex-direction: column;

    min-height: 100vh;

    header {

        padding: 20px;
        text-align: center;
        background-color: #333; }

    main {

        flex-grow: 1;

        display: flex;
        flex-direction: column;

        background-color: #ddd;

        aside {

            border-bottom: 2px solid black;
            background-color: #ddd;

            nav {
                a {
                    display: block;
                    padding: 15px;
                    text-decoration: none;
                    text-transform: uppercase;
                    color: black; } } }

        .page {

            flex-grow: 1; } }

    footer {

        padding: 20px;
        text-align: center;
        background-color: #333; } }


.active {
    background-color: royalblue; }

.add-new-product {

    .form {

        >p {

            display: flex;
            padding: 10px;

            label {
                flex-basis: 30%;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                padding-right: 20px; }
            input {
                max-width: 300px;
                flex-basis: 70%;
                line-height: 50px;
                height: 50px; }
            select {
                max-width: 300px;
                flex-basis: 70%;
                line-height: 50px;
                height: 50px; }
            .price-wrapper {

                display: flex;
                max-width: 300px;
                flex-basis: 70%;
                button {

                    height: 50px;
                    width: 50px;
                    border: none; }
                span {

                    height: 50px;
                    line-height: 50px;
                    text-align: center;
                    width: 50px; } } }
        .p-error, .p-button {
            justify-content: center;
            align-items: center; }
        .p-button {
            button {
                padding: 10px 20px; } } } }

.product-view, .transaction-view {

    padding: 20px;
    border-bottom: 2px solid black;

    background-color: #ddd; }

h2 {

    padding: 20px;
    text-align: center;
    background-color: #666; }

.product {
    padding: 20px;
    border-bottom: 2px solid black;

    .product-sell {
        padding: 20px;
        border-bottom: 2px solid black; } }
